"use client"

import { LineDashboardProvider, useLineDashboardContext } from "../context/LineDashboardProvider"
import { LineSummaryPanel } from "./LineSummaryPanel"
import { DataTable } from "./data-table"

function SummarySection() {
  const { data, isLoading, error } = useLineDashboardContext()

  if (isLoading) {
    return (
      <section className="rounded-lg border bg-card p-4 text-sm text-muted-foreground">
        Loading summary‚Ä¶
      </section>
    )
  }

  if (error) {
    return (
      <section className="rounded-lg border bg-destructive/10 p-4 text-sm text-destructive">
        {error.message}
      </section>
    )
  }

  return <LineSummaryPanel summary={data?.summary ?? null} />
}

export function LineDashboardPage({ lineId, initialData }) {
  return (
    <LineDashboardProvider lineId={lineId} initialData={initialData}>
      <div className="flex h-[calc(100vh-5rem)] flex-col gap-4">
        <SummarySection />
        <div className="flex-1">
          <DataTable lineId={lineId} />
        </div>
      </div>
    </LineDashboardProvider>
  )
}
