"use client"

import { numberFormatter } from "./data-table/utils/constants"

function formatUpdatedAt(raw) {
  if (!raw) return "‚Äî"
  const date = new Date(raw)
  if (Number.isNaN(date.getTime())) return "‚Äî"
  return date.toLocaleString()
}

const SUMMARY_FIELDS = [
  { key: "totalCount", label: "Total" },
  { key: "activeCount", label: "Active" },
  { key: "completedCount", label: "Completed" },
  { key: "pendingJiraCount", label: "Pending Jira" },
  { key: "lotCount", label: "Lots" },
]

export function LineSummaryPanel({ summary }) {
  if (!summary) {
    return (
      <section className="rounded-lg border bg-card p-4 text-sm text-muted-foreground">
        No summary data available.
      </section>
    )
  }

  return (
    <section className="grid gap-3 rounded-lg border bg-card p-4 md:grid-cols-3 lg:grid-cols-5">
      {SUMMARY_FIELDS.map(({ key, label }) => (
        <div key={key} className="flex flex-col">
          <span className="text-xs font-medium text-muted-foreground">{label}</span>
          <span className="text-lg font-semibold">
            {numberFormatter.format(Number(summary[key] ?? 0))}
          </span>
        </div>
      ))}
      <div className="md:col-span-3 lg:col-span-5">
        <span className="text-xs font-medium text-muted-foreground">Last updated</span>
        <span className="block text-sm">
          {formatUpdatedAt(summary.latestUpdatedAt)}
        </span>
      </div>
    </section>
  )
}
