"use client"

import * as React from "react"

async function requestLineDashboard(lineId) {
  const response = await fetch(`/api/line-dashboard?lineId=${encodeURIComponent(lineId)}`, {
    cache: "no-store",
  })

  if (response.status === 404) {
    return null
  }

  if (!response.ok) {
    const payload = await response.json().catch(() => ({}))
    const error = new Error(payload.error || "Failed to load line dashboard data")
    error.status = response.status
    throw error
  }

  return response.json()
}

export function useLineDashboardData(lineId, { initialData = null, enabled = true } = {}) {
  const [data, setData] = React.useState(initialData)
  const [error, setError] = React.useState(null)
  const [isLoading, setIsLoading] = React.useState(false)
  const [isRefreshing, setIsRefreshing] = React.useState(false)

  const shouldFetch = enabled && typeof lineId === "string" && lineId.length > 0

  React.useEffect(() => {
    setData(initialData)
    setError(null)
  }, [initialData, lineId])

  const refresh = React.useCallback(async () => {
    if (!shouldFetch) return null
    setIsRefreshing(true)
    try {
      const payload = await requestLineDashboard(lineId)
      setData(payload)
      setError(null)
      return payload
    } catch (err) {
      setError(err)
      throw err
    } finally {
      setIsRefreshing(false)
    }
  }, [lineId, shouldFetch])

  React.useEffect(() => {
    if (!shouldFetch) return
    let cancelled = false

    const run = async () => {
      setIsLoading(true)
      try {
        const payload = await requestLineDashboard(lineId)
        if (!cancelled) {
          setData(payload)
          setError(null)
        }
      } catch (err) {
        if (!cancelled) {
          setError(err)
        }
      } finally {
        if (!cancelled) {
          setIsLoading(false)
        }
      }
    }

    run()

    return () => {
      cancelled = true
    }
  }, [lineId, shouldFetch])

  return {
    data,
    error,
    isLoading,
    isRefreshing,
    refresh,
  }
}
