"use client"

import { cn } from "@/lib/utils"
import { toast } from "sonner"
import { Check, CalendarCheck2, CalendarX2, XCircle } from "lucide-react"

/**
 * ‚úÖ Î≥ÄÍ≤Ω ÏöîÏïΩ
 * - ÏÑ±Í≥µ / Ï∑®ÏÜå / Ïã§Ìå® Î™®Îëê Í∞ôÏùÄ Í∞ïÏ°∞ ÎîîÏûêÏù∏
 * - justify-content: "flex-start" + gap: 8px (Ïñë ÎÅù Î∞∞Ïπò ÎåÄÏã† Í∞ÑÍ≤© Ïú†ÏßÄ)
 * - ÏÉâÏÉÅÎßå Ï¥àÎ°ù / ÌååÎûë / Îπ®Í∞ïÏúºÎ°ú Íµ¨Î∂Ñ
 */

function normalizeNeedToSend(raw) {
  if (typeof raw === "number" && Number.isFinite(raw)) return raw
  if (typeof raw === "string" && raw.trim().length > 0) {
    const parsed = Number.parseInt(raw, 10)
    return Number.isFinite(parsed) ? parsed : 0
  }
  const numeric = Number(raw)
  return Number.isFinite(numeric) ? numeric : 0
}

export function NeedToSendCell({ meta, recordId, baseValue }) {
  const normalizedBase = normalizeNeedToSend(baseValue)
  const draftValue = meta.needToSendDrafts?.[recordId]
  const nextValue = draftValue ?? normalizedBase
  const isChecked = Number(nextValue) === 1
  const isSaving = Boolean(meta.updatingCells?.[`${recordId}:needtosend`])

  // ‚úÖ Í≥µÌÜµ Ïä§ÌÉÄÏùº
  const baseToastStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-start", // ÏôºÏ™Ω Ï†ïÎ†¨
    gap: "20px", // ÏïÑÏù¥ÏΩòÍ≥º ÌÖçÏä§Ìä∏ ÏÇ¨Ïù¥ Ïó¨Î∞±
    fontWeight: "600",
    fontSize: "14px",
    padding: "15px 20px",
    borderRadius: "8px",
    backgroundColor: "#f9fafb", // ÏùÄÏùÄÌïú Î∞∞Í≤Ω
  }

  // ‚úÖ 0 ‚Üí 1 : ÏòàÏïΩ ÏÑ±Í≥µ
  const showReserveToast = () =>
    toast.success("ÏòàÏïΩ ÏÑ±Í≥µ", {
      description: "E-SOP Inform ÏòàÏïΩ ÎêòÏóàÏäµÎãàÎã§.",
      icon: <CalendarCheck2 className="h-5 w-5 text-emerald-500" />,
      style: {
        ...baseToastStyle,
        color: "#065f46", // ÏßÑÌïú Ï¥àÎ°ù
      },
      duration: 1800,
    })

  // ‚úÖ 1 ‚Üí 0 : ÏòàÏïΩ Ï∑®ÏÜå
  const showCancelToast = () =>
    toast("ÏòàÏïΩ Ï∑®ÏÜå", {
      description: "E-SOP Inform ÏòàÏïΩ Ï∑®ÏÜå ÎêòÏóàÏäµÎãàÎã§.",
      icon: <CalendarX2 className="h-5 w-5 text-sky-600" />,
      style: {
        ...baseToastStyle,
        color: "#1e40af", // ÌååÎûë Í∞ïÏ°∞
      },
      duration: 1800,
    })

  // ‚úÖ Ïã§Ìå®
  const showErrorToast = (msg) =>
    toast.error("Ï†ÄÏû• Ïã§Ìå®", {
      description: msg || "Ï†ÄÏû• Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.",
      icon: <XCircle className="h-5 w-5 text-red-500" />,
      style: {
        ...baseToastStyle,
        color: "#991b1b", // ÏßÑÌïú Îπ®Í∞ï
      },
      duration: 3000,
    })

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  // ÌÜ†Í∏Ä Ìï∏Îì§Îü¨
  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const handleToggle = async () => {
    if (isSaving) return

    const targetValue = isChecked ? 0 : 1
    const key = `${recordId}:needtosend`

    if (targetValue === normalizedBase) {
      meta.removeNeedToSendDraftValue?.(recordId)
      meta.clearUpdateError?.(key)
      return
    }

    meta.setNeedToSendDraftValue?.(recordId, targetValue)
    meta.clearUpdateError?.(key)

    const ok = await meta.handleUpdate?.(recordId, { needtosend: targetValue })

    if (ok) {
      meta.removeNeedToSendDraftValue?.(recordId)
      if (targetValue === 1) showReserveToast()
      else showCancelToast()
    } else {
      const msg = meta.updateErrors?.[key]
      meta.removeNeedToSendDraftValue?.(recordId)
      showErrorToast(msg)
    }
  }

  return (
    <div className="flex items-center gap-1">
      {/* ‚úÖ ÏõêÌòï Ï≤¥ÌÅ¨Î≤ÑÌäº */}
      <button
        type="button"
        onClick={handleToggle}
        disabled={isSaving}
        className={cn(
          "inline-flex h-5 w-5 items-center justify-center rounded-full border transition-colors",
          isChecked
            ? "bg-emerald-500 border-emerald-500"
            : "border-muted-foreground/30 hover:border-emerald-300",
          isSaving && "opacity-60 cursor-not-allowed"
        )}
        title={isChecked ? "Need to send" : "Not selected"}
        aria-label={isChecked ? "Need to send" : "Not selected"}
      >
        {isChecked && <Check className="h-3 w-3 text-white" strokeWidth={3} />}
      </button>
    </div>
  )
}

export default NeedToSendCell
