"use client"

import { Check } from "lucide-react"

function normalizeBinaryFlag(raw) {
  if (raw === 1 || raw === "1") return true
  if (raw === "." || raw === "" || raw == null) return false
  const numeric = Number(raw)
  return Number.isFinite(numeric) ? numeric === 1 : false
}

export function SendJiraCell({ value }) {
  const sent = normalizeBinaryFlag(value)
  return (
    <span
      className={[
        "inline-flex h-5 w-5 items-center justify-center rounded-full border",
        sent ? "bg-emerald-500 border-emerald-500" : "border-muted-foreground/30",
      ].join(" ")}
      title={sent ? "Sent to JIRA" : "Not sent"}
      aria-label={sent ? "Sent to JIRA" : "Not sent"}
      role="img"
    >
      {sent ? <Check className="h-3 w-3 text-white" strokeWidth={3} /> : null}
    </span>
  )
}
